<template>
    <div class="hongBaoHuanWarp">
        <header>
            <router-link to="/benefit">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <section>
                <span>红包兑换</span>
            </section>
        </header>
        <div class="exchange_code">
            <input type="text" placeholder="请输入兑换码" class="exchange_input" v-model="one">
            <div class="input_container ">
                <input type="text" placeholder="请输入验证码" class="input_style" v-model="two">
                <div class="img_change_img">
                    <img :src="img" alt="">
                    <div class="change_img" @click="changeImg">
                        <p>看不清</p>
                        <p>换一张</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="determine" @click="dui_huan">兑换</div>

        <!--弹窗-->
        <div class="alet_container" v-if="isShow">
            <section class="tip_text_container">
                <div class="tip_icon">
                    <span></span>
                    <span></span>
                </div>
                <p class="tip_text">无效的兑换码</p>
                <div class="confrim" @click="goTan1">确定</div>
            </section>
        </div>
    </div>
</template>

<script>
    export default {
        name: "HongBaoHuan",
        data(){
            return {
                img:'',
                one:'',
                two:'',
                isShow:false
            }
        },
        created(){
            this.user_id = localStorage.getItem("user_id");
            this.axios.post('/v1/captchas').then((res)=>{
                this.img=res.code
                console.log(res)
            })
        },
        methods:{
            changeImg(){
                this.axios.post('/v1/captchas').then((res)=>{
                    this.img=res.code
                    console.log(res)
                })
            },
            dui_huan(){
                 this.axios.post("/v1/users/"+this.user_id+ "/hongbao/exchange",{
                     exchange_code:this.one,
                     captcha_code:this.two
                 }).then(res=>{
                     console.log(res)
                     if (res.message =="无效的兑换码") {
                         //弹出提示框
                         this.isShow=true;
                     }

                 }).catch(err=>{})
            },
            goTan1(){
                this.isShow=false;
            }
        }
    }
</script>

