    }
    header>section span{
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }

    .exchange_code{
        margin-top: .7rem;
        padding: 0 .5rem;
    }
   .exchange_input{
       width: 96%;
       font-size: .7rem;
       color: #666;
       padding: .6rem .4rem;
       border-radius: .15rem;
       outline: none;
       border: none;
   }
    .input_container{
        height: 2.2rem;
        display: flex;
        margin-top: .7rem;
    }
