       justify-content: center;
   }
    .change_img>:nth-child(1){
        font-size: .55rem;
        color: #666;
    }
    .change_img>:nth-child(2){
        color: #3b95e9;
        margin-top: .2rem;
        font-size: .55rem;
    }
    .determine{
        background-color: #ccc;
        font-size: .7rem;
